import React from 'react'
import './App.css'
import { TextField, Button, Typography } from '@mui/material'

import { Configuration, OpenAIApi } from "openai"

const configuration = new Configuration({
  apiKey: atob('c2stbDhnNUR6VFZ0ZzJUdTF0c0Rxc3dUM0JsYmtGSmFmY1ZzZHpvT2tuRHZHSWtHQzFB'),
})

const openai = new OpenAIApi(configuration)

function App() {

  const [request, setRequest] = React.useState('')
  const [result, setResult] = React.useState('')

  async function onSubmit() {
    const completion = await openai.createCompletion({
      model: "text-davinci-002",
      prompt: request,
      temperature: 0.6,
    });
    if (completion.data.choices!.length > 0) {
      setResult(completion.data.choices![0].text!)
    } else {
      setResult('Nothing')
    }
    setRequest('')
  }

  return (
    <div className='App'>

      <Typography className='AppItem' variant="h3">Ask me anything</Typography>

      <TextField
        className='AppItem'
        id="outlined-textarea"
        label="Request"
        placeholder="Request"
        multiline
        value={request}
        onChange={(e) => setRequest(e.target.value)}
        fullWidth={true}
      >
        Request
      </TextField>

      <Button className='AppItem' onClick={onSubmit}>Send</Button>

      <Typography className='AppItem' variant="body1">{result}</Typography>

    </div>
  )
}

export default App
